.productpage-section {
	.fx-wrap {
		top: 20px;
		left: 10px; } }

.baler-feature-box {
	padding: 35px;
	border: 2px solid #e6eaf0;
	display: flex;
	flex-direction: column;
	width: 100%;
	.img {
		margin-bottom: 35px;
		img {
			height: 100px;
			display: block;
			margin-left: auto;
			margin-right: auto; } }
	.text {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		h4 {
			font-size: 24px;
			color: $primary-color;
			text-transform: uppercase;
			font-weight: bold;
			min-height: 56px;
			margin-bottom: 20px; }
		p {
			font-size: 14px;
			line-height: 1.5;
			margin-bottom: 0; } } }

.baler-feature-row {
	> * {
		display: flex; } }

.baler-feature-section {
	padding-top: 128px;
	.parallax-container {
		.fx-wrap {
			top: 30px;
			right: 10px; } } }

.baler-rope-section {
	padding-top: 130px;
	.title__ {
		margin-bottom: 40px;
		h3 {
			letter-spacing: 10px; } }
	img {
		width: 100%; }
	p {
		font-size: 18px;
		line-height: 1.5; } }
@media (max-width: 1199.83px) {
	.baler-rope-section {
		.title__ {
			margin-bottom: 20px;
			h3 {
				font-size: 30px; } }
		p {
			font-size: 16px; } } }
@media (max-width: 991.82px) {
	.baler-feature-box {
		.text {
			text-align: center;
			h4 {
				min-height: unset;
				margin-bottom: 20px; } } }
	.baler-rope-section {
		img {
			margin-bottom: 20px; } } }

@media (max-width: 767.82px) {
	.baler-rope-section {
		padding-top: 80px; } }
@media (max-width: 575.82px) {
	.baler-feature-box {
		padding: 15px; } }



