.productpage-section {
	padding: 120px 0;
	._head {
		margin-bottom: 150px;
		img {
			margin-left: auto;
			margin-right: auto;
			display: block;
			max-width: 100%; } }
	.title__ {
		p {
			font-size: 18px;
			margin-bottom: 0; } } }

.product-box {
	position: relative;
	._content {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border: 2px solid #e6eaf0;
		transition: all .3s ease-in-out; }
	._title {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		width: 100%;
		padding: 30px;
		padding-bottom: 0;
		h3 {
			position: absolute;
			bottom: 0;
			left: 0;
			margin-bottom: 0;
			padding: 0 40px;
			padding-left: 50px;
			color: $primary-color;
			font-size: 24px;
			transition: all .3s ease-in-out;
			text-transform: uppercase;
			z-index: 1; }
		h2 {
			position: relative;
			line-height: .8;
			margin-bottom: 0;
			font-size: 120px;
			font-weight: bold;
			color: rgba($primary-color, .15);
			font-weight: bold;
			transition: all .3s ease-in-out; } }
	._desc {
		opacity: 0;
		z-index: 1;
		transition: all .3s ease-in-out;
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 35px;
		padding-top: 0;
		p {
			line-height: 1.3;
			font-size: 14px;
			color: rgba(#fff, .76); }
		a {
			font-size: 12px;
			color: $yellow-color;
			font-weight: bold;
			text-decoration: underline;
			letter-spacing: 1px; } }
	._img {
		position: absolute;
		bottom: 35px;
		height: 47%;
		left: 0;
		width: 100%;
		display: flex;
		transition: all .3s ease-in-out;
		.fixed-thumb {
			height: auto;
			padding-bottom: 0; } }

	&:hover {
		._title {
			h3 {
				color: #fff; }
			h2 {
				color: rgba(#fff, .15); } }
		._desc {
			opacity: 1; }
		._img {
		 	opacity: 0; } }



	&:hover {
		._content {
			border-color: transparent; } } }

.product-list {
	> div {
		margin-left: -2px;
		margin-top: -2px;
		&:hover {
			z-index: 2; } } }
.products-pag {
	justify-content: center;
	ul {
		display: flex;
		justify-content: center;
		li {
			margin-right: 20px;
			&:last-child {
				margin-right: 0; } }
		a, span {
			font-size: 24px;
			color: $primary-color, .4;
			opacity: .4;
			font-weight: bold;
			font-family: $font-2;
			&:hover {
				opacity: 1; } }
		span {
			color: $yellow-color;
			opacity: 1; } } }
.letushelp-section {
	margin-bottom: 130px;
	.title__ {
		margin-bottom: 60px;
		h3 {
			margin-bottom: 10px;
			letter-spacing: 7px; }
		p {
			font-size: 18px; } } }

.letushelp-box {
	._img {
		position: absolute;
		top: 0;
		left: 0;
		height: 50%;
		width: 100%;
		display: flex;
		align-items: flex-end;
		justify-content: center; }
	._desc {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 20px;
		text-align: center;
		a {
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 12px;
			color: #fff;
			font-weight: bold;
			text-decoration: underline;
			text-transform: uppercase;
			opacity: 0;
			z-index: 1;
			transition: all .3s ease-in-out; }
		p {
			line-height: 1.3;
			position: relative;
			font-size: 18px;
			margin-bottom: 0;
			transition: all .3s ease-in-out; } }
	&:hover {
		._img {
			img {
				filter: brightness(0) invert(1); } }
		._desc {
			a {
				opacity: 1; }
			p {
				opacity: 0; } } } }

@media (max-width: 767.82px) {
	.productpage-section {
		padding: 80px 0;
		._head {
			margin-bottom: 30px; }

		.title__ {
			h3 {
				font-size: 35px;
				margin-bottom: 20px; }
			p {
				font-size: 14px; } } }
	.product-box {
		._title {
			padding: 15px;
			padding-bottom: 0;
			h2 {
				font-size: 100px; }

			h3 {
				font-size: 20px; } }
		._img {
			bottom: 10%; }
		._desc {
			padding: 15px;
			padding-top: 0; } }

	.letushelp-section {
		margin-bottom: 80px;
		.title__ {
			margin-bottom: 40px;
			p {
				font-size: 14px; } } } }
@media (max-width: 575.82px) {
	.product-list {
		width: 300px;
		max-width: 100%;
		margin-left: auto;
		margin-right: auto; }

	.letushelp-box {
		._desc {
			p {
				font-size: 14px; } } }
	.productpage-section {

		.title__ {
			h3 {
				font-size: 25px;
				margin-bottom: 20px; } } } }



