.site-footer {
	background: rgb(0,55,124);
	background: -webkit-linear-gradient(bottom left, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);
	background: -o-linear-gradient(bottom left, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);
	background: linear-gradient(to top right, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);

	padding-top: 70px;
	padding-bottom: 100px;

	h4 {
		font-size: 13.3px;
		font-weight: bold;
		font-family: $font-1;
		color: $yellow-color; }

	ul {
		li {
			a {
				color: #fff;
				font-size: 12px; } } }
	p {
		font-size: 12px;
		color: #fff; }

	@media (max-width: 767.82px) {
		padding-top: 70px;
		padding-bottom: 50px; } }

.site-footer-2 {
	background: rgb(0,55,124);
	background: -webkit-linear-gradient(bottom left, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);
	background: -o-linear-gradient(bottom left, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);
	background: linear-gradient(to top right, rgba(0,55,124,1) 0%, rgba(0,66,134,1) 35%, rgba(0,122,192,1) 100%);

	padding-top: 70px;
	padding-bottom: 100px;
	.f-row {
		> * {
			display: flex; } }
	.logo {
		padding-top: 20px;
		img {
			width: 200px;
			max-width: 100%;
			max-height: 100%; } }
	.footer-block {
		width: 100%;
		text-align: center;
		border-left: 2px solid rgba(#fff, .3);
		padding: 20px 40px;
		.img {
			height: 51px;
			display: flex;
			justify-content: center;
			align-items: flex-end;
			margin-bottom: 25px;
			img {
				max-height: 100%; } }

		h4 {
			font-family: $font-1;
			color: $yellow-color;
			font-size: 14px; }
		p {
			font-size: 12px;
			color: #fff; } }
	@media (max-width: 991.82px) {
		padding-top: 80px;
		.logo {
			margin-bottom: 30px; }
		.footer-block {
			padding: 10px 20px;
			padding-bottom: 0;
			.img {
				max-height: 30px; } } }
	@media (max-width: 767.82px) {
		padding-top: 40px;
		padding-bottom: 40px;
		.footer-block {
			border: none; } } }



