.contact-page-section {
	padding: 130px 0;
	h4 {
		font-size: 24px;
		font-weight: bold;
		color: $primary-color;
		margin-bottom: 25px; }
	p {
		font-size: 14px;
		line-height: 1.5; }
	.contact-block {
		display: flex;
		margin-bottom: 20px;
		.img {
			flex-shrink: 0;
			width: 30px;
			padding-top: 10px;
			color: #2b71ff; }
		.text {
			p {
				margin-bottom: 0;
				font-size: 14px;
				&:first-child {
					color: $primary-color; } } } }
	.ct-row {
		margin-left: -10px;
		margin-right: -10px;
		> [class*="col-"] {
			padding-left: 10px;
			padding-right: 10px; } }
	.contact-form {
		.form-group {
			margin-bottom: 15px; }
		.form-control {
			border-color: #2b71ff;
			border-radius: 10px;
			height: 47px;
			font-size: 13px; }
		textarea.form-control {
			height: 150px; } }
	.thumb {
		margin-top: 100px; } }

@media (max-width: 991.82px) {
	.contact-page-section {
		.contact-form {
			margin-top: 20px; } } }
@media (max-width: 767.82px) {
	.contact-page-section {
		padding: 80px 0;
		.thumb {
			margin-top: 40px; } } }



