.newspage-section {
	padding-top: 87px;
	padding-bottom: 100px; }
.news-card-2-list {
	.news-card-2 {
		margin-bottom: 80px; } }
.news-card-2 {
	position: relative;
	._title {
		margin-bottom: 30px;
		h3 {
			font-size: 24px;
			color: $primary-color;
			font-weight: bold;
			line-height: 1.5;
			text-transform: uppercase; }
		p {
			font-size: 13px;
			margin-bottom: 0;
			span {
				display: inline-block; }
			span:first-child {
				color: $primary-color;
				margin-right: 20px; }
			span:last-child {
				color: rgba($yellow-color, .8); } } }
	._img {
		.fixed-thumb {
			padding-bottom: 53.704%; } }
	._desc {
		padding: 30px;
		p {
			font-size: 13px;
			color: rgba(black, .8);
			margin-bottom: 30px; }
		a {} } }

.news-sidebar {
	max-width: 100%;
	width: 260px;
	margin-left: auto;
	margin-right: auto; }
.news-sidebar-search {
	margin-bottom: 50px;
	form {
		display: flex;
		border: 2px solid #b7b7b7;
		input {
			flex-grow: 1;
			border: none;
			background: none;
			height: 38px;
			padding: 0 15px;
			font-size: 13px; }
		button {
			background: none;
			height: 38px;
			width: 38px;
			border: none;
			border-left: 2px solid #b7b7b7;
			color: #b7b7b7;
			transition: all .3s ease;
			&:hover {
				background: #b7b7b7;
				color: $primary-color; } } } }
.news-sidebar-block {
	h5 {
		font-size: 13px;
		color: rgba(black, .5);
		font-weight: 400;
		font-family: $font-1; }
	ul {
		li {
			border-bottom: 1px solid #d7d7d7;
			a {
				display: block;
				padding: 15px 0px;
				font-size: 13px;
				color: rgba(black, .5); } } } }

.newspage-detail-section {
	padding: 84px 0;
	.title__ {
		margin-bottom: 58px;
		h3 {
			font-weight: bold;
			font-size: 30px; }
		p {
			margin-bottom: 0;
			font-size: 13px;
			span {
				display: inline-block;
				&:first-child {
					margin-right: 20px;
					color: $primary-color; }
				&:last-child {
					color: $yellow-color; } } } } }
.news-detail-content {
	text-align: center;
	p {
		margin-bottom: 30px; }
	img {
		margin-bottom: 30px;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%; } }

.back-to-news {
	margin-top: 60px;
	a {
		color: $primary-color;
		text-decoration: underline;
		font-size: 24px; } }

@media (max-width: 991.82px) {
	.news-sidebar {
		width: 100%; } }
@media (max-width: 767.82px) {
	.news-card-2 {
		._desc {
			padding-left: 0;
			padding-right: 0; } } }
@media (max-width: 575.82px) {
	.news-card-2 {
		._title {
			h3 {
				font-size: 20px; } } }

	.newspage-detail-section {
		.title__ {
			h3 {
				font-size: 25px; } } } }



