.product-detail-content {
	padding-top: 100px;
	.imgs {
		display: flex;
		align-items: flex-start; }
	._list {
		width: 65px;
		flex-shrink: 0;
		.fixed-thumb {
			cursor: pointer;
			margin-bottom: 10px;
			&.active {
				box-shadow: 0 2px 5px rgba(black, .3); } } }
	._main-img {
		width: calc(100% - 65px);
		text-align: center;
		padding-left: 10px;
		padding-right: 10px; }


	.text {
		h3 {
			font-weight: bold;
			color: $primary-color;
			font-size: 44px;
			letter-spacing: 10px; }
		p {
			font-size: 18px;
			line-height: 1.5; } } }

.detail-feature-box {
	position: relative;
	border: 3px solid #e6eaf0;
	padding: 35px;

	.img {
		margin-bottom: 25px; }
	h4 {
		font-size: 24px;
		color: $primary-color;
		margin-bottom: 25px;
		text-transform: uppercase; }
	p {
		font-size: 14px;
		line-height: 1.5; }
	a {
		font-size: 12px;
		font-weight: bold;
		color: $yellow-color;
		text-decoration: underline;
		text-transform: uppercase; } }

.detail-feature-row {
	margin-bottom: 100px;
	margin-top: 75px;
	transform: translateX(1px);
	> [class*="col-"] {
		display: flex;
		margin-left: -3px;
		margin-top: -3px; }
	.detail-feature-box {
		width: 100%;
		&._p1 {
			padding-top: 120px; } } }
@media (max-width: 1199.82px) {
	.detail-feature-row {
		.detail-feature-box {
			&._p1 {
				padding-top: 35px; } } } }

@media (max-width: 991.82px) {
	.product-detail-content {
		.imgs {
			margin-bottom: 20px; }
		.text {
			h3 {
				letter-spacing: initial;
				font-size: 20px; } } } }



