.ham {
	// background: green
	position: relative;
	display: block;
	cursor: pointer;
	> [class*="_"] {
		width: 25px;
		margin-left: auto;
		margin-right: 0;
		height: 5px;
		background: #fff;
		margin-bottom: 2px;
		transform: skewX(-30deg);
		position: relative;
		&:last-child {
			margin-bottom: 0; } }
	._1 {
		left: 6px; }
	._2 {
		left: 3px;
		transition: all .3s ease-in-out; }
	&:not(.collapsed) {
		._2 {
			width: 0px; } } }



.site-header {
	background: $primary-color;
	.header-content {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	.logo {
		max-width: 150px; }
	.header-content > .nav {
		display: flex;
		align-items: center;
		> ul {
			display: flex;
			> li {
				> a {
					padding: 50px 20px;
					display: block;
					font-size: 12px;
					font-family: $font-2;
					color: #fff;
					text-transform: uppercase;
					&:hover {
						color: $yellow-color; } } } }
		._search {
			a {
				display: block;
				padding: 50px 20px;
				padding-right: 0;
				font-size: 12px;
				color: #fff;
				width: 35px;
				text-align: right;
				i:first-child {
					display: none; }
				i:last-child {
					display: block; }
				&.collapsed {
					i:first-child {
						display: block; }
					i:last-child {
						display: none; } } } }
		._ham {
			padding-left: 20px; } } }

.site-header._white {
	background: #fff;
	.header-content {
		>.nav {
			>ul {
				>li {
					>a {
						color: #fff;
						color: $primary-color; } } }
			._search a {
				color: $primary-color; } } }

	.ham>[class*=_] {
		background: $primary-color; } }
.site-header {
	position: relative;
	._mobile {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		z-index: 2;
		background: $primary-color;
		.hr {
			height: 1px;
			width: 100%;
			background: $primary-color; }
		ul {
			li {
				a {
					text-align: right;
					display: table;
					margin-left: auto;
					margin-right: auto;
					font-family: $font-2;
					font-size: 14px;
					color: $yellow-color;
					text-transform: uppercase;
					padding: 20px 0;
					position: relative;
					letter-spacing: 1px;
					&:hover {
						color: #fff; }
					&:before {
						position: absolute;
						content: '';
						width: 100vw;
						height: 1px;
						bottom: 0px;
						right: 50%;
						transform: translateX(50%);
						background: rgba($yellow-color, .3); } }
				&:last-child {
					a {
						&:before {
							display: none; } } } } } } }



.site-header {
	._search-collapse {
		position: absolute;
		top: 100%;
		left: 0;
		background: $primary-color;
		width: 100%;
		z-index: 10;
		form {
			display: flex;
			padding: 20px 0;
			input {
				flex-grow: 1;
				background: none;
				border: none;
				text-align: center;
				color: #fff; }
			button {
				width: 40px;
				height: 40px;
				color: #fff;
				background: none;
				transition: all .3s ease-in-out;
				border: 1px solid #fff;
				&:hover {
					background: #fff;
					color: $primary-color; } } } } }

.submega {
	position: fixed;
	top: 118px;
	left: 0;
	width: 100%;
	z-index: 10;
	background: #e0e0e0;
	display: flex;
	.nav {
		width: 300px;
		flex-shrink: 0;
		flex-direction: column;
		flex-shrink: 0;
		li {
			height: 25%;
			display: flex;
			a {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				background: $primary-color;
				text-align: center;
				border-bottom: 1px solid rgba(#fff, .5);
				font-size: 18px;
				text-transform: uppercase;
				font-size: 18px;
				font-family: $font-2;
				color: #fff;
				padding: 40px 20px;
				&.active {
					background: $yellow-color;
					color: $primary-color;
					text-decoration: underline; } }
			&:last-child {
				a {
					border-bottom: 0; } } } }
	.tab-content {
		flex-grow: 1;
		position: relative;
		width: calc(100% - 300px);
		padding: 50px 100px;
		.tab-pane {
			position: relative;
			align-items: center;
			justify-content: center;
			.swiper-container {
				width: 100%; } } } }

.submega-swiper-nav {
	position: absolute;
	top: 50%;
	left: -50px;
	transform: translate(-50%, -50%);
	cursor: pointer;
	&:focus {
		outline: none; }
	&.next {
		left: auto;
		right: -50px;
		transform: translate(50%, -50%); }
	&.prev {}
	&.swiper-button-disabled {
		opacity: .2; } }
.submega-swiper-item {
	h4 {
		font-size: 24px;
		font-weight: bold;
		color: $primary-color;
		text-align: center;
		margin-bottom: 0;
		margin-top: 25px;
		text-transform: uppercase; } }
.submega-parent {
	.submega {
		transform: translateY(10px);
		opacity: 0;
		pointer-events: none;
		transition: all .3s ease-in-out; }
	&:hover {
		.submega {
			transform: none;
			opacity: 1;
			pointer-events: auto; } } }


@media (max-width: 991.82px) {
	.site-header .header-content>.nav ._search a {
		padding-top: 30px;
		padding-bottom: 30px; } }




