.homepage-wrapper {
	background: rgb(8,25,49);
	background: -webkit-linear-gradient(rgba(8,25,49,1) 0%, rgba(20,64,125,1) 35%, rgba(177,185,196,1) 100%);
	background: -o-linear-gradient(rgba(8,25,49,1) 0%, rgba(20,64,125,1) 35%, rgba(177,185,196,1) 100%);
	background: linear-gradient(rgba(8,25,49,1) 0%, rgba(20,64,125,1) 35%, rgba(177,185,196,1) 100%); }
.home-banner-section {
	height: 100vh;
	.text {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(-50%);
		h1 {
			font-size: 60px;
			font-weight: bold;
			color: $yellow-color;
			letter-spacing: 7px; } } }
.home-about-section {
	padding-top: 120px;
	.parallax-container {
		.fx-wrap {
			left: 0;
			top: 30%; } }
	.title__ {
		margin-bottom: 70px; }
	.text {
		h5 {
			font-weight: bold;
			font-family: $font-1;
			color: $yellow-color; }
		p {
			color: #fff;
			font-size: 14px; }
		.ic-gr {
			img {
				margin-bottom: 10px; }
			p {
				font-weight: bold;
				font-size: 14.5px;
				text-transform: uppercase; } }
		&._1 {
			h5 {
				margin-top: 29px; } } }
	.fixed-thumb {
		box-shadow: 0 8px 25px rgba(black, .2); } }

.home-video-section {
	padding-top: 100px;
	.parallax-container {
		.fx-wrap {
			top: 18%;
			right: 0; } }
	.title__ {
		margin-bottom: 80px; } }
.home-video-nav {
	position: absolute;
	top: 50%;
	transform: translatey(-50%);
	z-index: 1;
	font-size: 12.5px;
	color: #fff;
	font-weight: bold;
	text-transform: uppercase;
	letter-spacing: 1px;
	display: flex;
	align-items: center;
	cursor: pointer;
	&.prev {
		left: 10px;
		img {
			margin-right: 5px; } }
	&.next {
		right: 10px;
		img {
			margin-left: 5px; } } }
.video-swiper-item {
	.fixed-thumb {
		padding-bottom: 151.467%;
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(black,  .3);
			z-index: 2; } }
	._title {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		width: 100%;
		padding: 40px;
		transition: all .3s ease-in-out;
		h5 {
			font-size: 18px;
			line-height: 1.5;
			margin: 0;
			color: #fff;
			font-weight: normal;
			text-transform: uppercase; } }
	._tag {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translateY(100%) rotate(-90deg);
		transform-origin: top left;
		transition: all .3s ease-in-out;
		h6 {
			padding: 40px 25px;
			font-size: 11px;
			font-weight: bold;
			font-family: $font-1;
			color: #fff;
			text-transform: uppercase;
			letter-spacing: 5px; } }
	.overlay-hover {
		opacity: 0;
		transition: all .3s ease-in-out;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;
		width: 100%;
		height: 100%;
		background: rgba($yellow-color, .7);
		display: flex;
		justify-content: center;
		align-items: center;
		._search-ic {
			display: flex;
			position: relative;
			justify-content: center;
			align-items: center;
			color: #fff;
			width: 100px;
			height: 100px;
			img {
				position: relative; }
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: #fff;
				border-radius: 50%;
				transform: scale(.5);
				opacity: 0;
				transition: all .3s ease; } } }

	&:hover {
		._title {
			opacity: 0; }
		._tag {
			opacity: 0; }
		.overlay-hover {
			opacity: 1;
			._search-ic:before {
				opacity: 1;
				transform: none; } } } }

.home-distributor-section {
	padding-top: 170px;
	.parallax-container {
		.fx-wrap {
			top: 10%;
			left: 0; } }
	._title {
		display: flex;
		align-items: flex-end;
		margin-bottom: 75px;
		img {
			max-width: 100%;
			margin-right: 10px; }
		h4 {
			font-size: 40px;
			color: $yellow-color;
			text-transform: uppercase;
			margin: 0;
			span {
				color: $primary-color;
				display: block; } } }

	p {
		color: #fff; } }

.home-news-section {
	padding-top: 90px;
	.parallax-container {
		.fx-wrap {
			top: 18%;
			right: 0; } }
	.title__ {
		margin-bottom: 90px; } }

.news-card {
	.text {
		padding-top: 20px;
		h5 {
			font-size: 18px;
			color: $primary-color;
			font-weight: bold;
			font-family: $font-1;
			text-transform: uppercase;
			margin-bottom: 20px; }
		p {
			font-size: 13px;
			font-weight: normal;
			color: $primary-color; }
		p._date {
			margin-bottom: 12px;
			line-height: 1; }
		a.read-more {
			font-size: 12.84px;
			color: $primary-color;
			font-weight: bold;
			text-transform: uppercase; } }
	&:hover {
		.text {
			h5 {
				color: $yellow-color; }
			a.read-more {
				color: $yellow-color;
				text-decoration: underline; } } } }


.home-intouch-section {
	padding-top: 160px;
	padding-bottom: 144px;
	.parallax-container {
		.fx-wrap {
			top: 18%;
			right: 0; } }
	.title__ {
		margin-bottom: 90px; }

	.intouch-row {
		margin-left: -4px;
		margin-right: -4px;
		> [class*="col-"] {
			padding-left: 4px;
			padding-right: 4px; } }
	.form-group {
		margin-bottom: 8px; }
	.form-control {
		border: none;
		font-size: 13.35px; }
	input.form-control {
		height: 50px; }

	textarea.form-control {
		height: 108px; }

	.site-btn {
		margin-top: 20px; }


	p {
		margin-bottom: 0;
		color: $primary-color;
		font-size: 14px; } }



@media (max-width: 1199.82px) {
	.intouch-form {
		margin-top: 30px; } }
@media (max-width: 991.82px) {
	.video-swiper-item {
		._title {
			padding: 20px;
			h5 {} }
		._tag {
			h6 {
				padding: 20px 15px; } } }


	.home-distributor-section {
		._title {
			img {
				width: 200px; }
			h4 {
				font-size: 32px; } } } }
@media (max-width: 767.82px) {
	.home-banner-section {
		.text {
			h1 {
				font-size: 40px; } } }

	.home-about-section {
		padding-top: 80px;
		.title__ {
			margin-bottom: 30px; }
		.text {
			&._1 {
				h5 {
					margin-top: 0; } } } }

	.home-video-section {
		.title__ {
			margin-bottom: 40px; } }

	.home-distributor-section {
		padding-top: 80px;
		._title {
			margin-bottom: 40px;
			img {
				width: 150px; } } }

	.home-news-section {
		.title__ {
			margin-bottom: 40px; } }

	.home-intouch-section {
		padding-top: 100px;
		.title__ {
			margin-bottom: 40px; } } }
@media (max-width: 575.82px) {
	.home-banner-section {
		height: 40vh;
		.text {
			h1 {
				font-size: 25px; } } }


	.home-about-section {
		.text {
			.ic-gr {
				display: inline-block;
				width: 100px; }
			h5 {
				font-size: 20px; } } }

	.home-distributor-section {
		._title {
			img {
				max-width: 100px; }
			h4 {
				font-size: 20px; } } }
	.home-intouch-section {
		padding-top: 70px; } }













